import { TrinityEngine } from '../engine/4.0';
import './SearchBar.css';
let SearchBar = {};

let data, ctrl, back;

SearchBar.init = function(items, controls, callback) {
    data = items;
    ctrl = controls;
    back = callback;
    var t = document.querySelector('input[placeholder]');
    var r = document.querySelector('.SearchBox');
    t.addEventListener('input', function() {
        r.innerHTML = '';
        if(t.value !== '') {
            filter(t.value.toLowerCase());
        }
    });
};

function filter(q) {
    let n = 0;
    Object.keys(data).forEach(function(name) {
        if(name.toLocaleLowerCase().indexOf(q) > -1 && n < 5) {
            
            var e = document.createElement('div');
            e.className = 'SearchBoxItem';
            e.innerHTML = name;
            e.addEventListener('click', function() {
                TrinityEngine.tweenControls(
                    'EXACT', ctrl,
                    data[name].visit_pose.target,
                    data[name].visit_pose.camera,
                    2000, back
                );
            });

            document.querySelector('.SearchBox').appendChild(e);

            n++;
        }
    })
};

export default SearchBar;