import './ToastBox.css';

let ToastBox = {};

ToastBox.root = null;

ToastBox.init = function() {
    ToastBox.root = document.querySelector('.ToastBox');
};

ToastBox.append = function(message, timeout = 5000, delay = 0) {
    if(ToastBox.root === null) { return; }
    setTimeout(function() {
        // new parent element
        let elem = document.createElement('div');
        elem.className = 'ToastBoxMessage';
        // new text element
        let text = document.createElement('div');
        text.className = 'ToastBoxMessageText';
        text.innerHTML = message;
        // new close element
        let done = document.createElement('div');
        done.className = 'ToastBoxMessageDone';
    
        done.addEventListener('click', function() {
            ToastBox.close(elem, true);
        });
    
        elem.appendChild(done);
        elem.appendChild(text);
        ToastBox.root.appendChild(elem);

        ToastBox.update();
        // disappear message
        setTimeout(function() {
            ToastBox.close(elem, false);
        }, timeout);
    }, delay);
};

let last_height = 0;

ToastBox.update = function() {
    var e = ToastBox.root;
    var h = e.childNodes.length * 46;
    e.style.height = h + 'px';
    if(last_height < h) {
        e.style.transition = 'top 0.3s';
    } else { e.style.transition = 'none'; }
    e.style.top = window.innerHeight - h + 'px';
    last_height = h;
};

ToastBox.close = function(elem, fast = false) {
    if(fast) {
        if(elem) { elem.remove(); }
        ToastBox.update();
    } else {
        elem.style.opacity = 0;
        setTimeout(function() {
            if(elem) { elem.remove(); }
            ToastBox.update();
        }, 300);
    }
}

window.addEventListener('resize', ToastBox.update);

export default ToastBox;