import { Component } from 'react';
import './App.css';
import { TrinityEngine, THREE } from './engine/4.0/';
import StallData from './bin/StallData';
import ItemsData from './bin/ItemsData';
import ToastBox from './modules/ToastBox';
import SearchBar from './modules/SearchBar';
import ItemsLoader from './modules/ItemsLoader';

import LoadingScreen from "./bin/logo/arimac";

// ====================================================================================

class App extends Component {

	componentDidMount() {


		LoadingScreen.start('#000', function() {
			ToastBox.init();
			SearchBar.init(ItemsData, modules.controls, checkToLoad);
			document.querySelector('#root').appendChild(modules.renderer.domElement);
			preLoad();
		})



	};

	render() {
		return (
			<div>
				<input type="text" spellCheck="false" placeholder="Search Items, Products, Stalls..." />
				<div className="SearchBox"></div>

				<div className="ToastBox"></div>

				<div className="DetailBox">
					<div className="DetailBoxHead"></div>
					<div className="DetailBoxText"></div>
					<div className="DetailBoxLink">Visit Store</div>
					<div className="DetailBoxBack">Exit Explore Mode</div>
				</div>

				<div className="arimac_load"></div>
				<div className="arimac_logo arimac_logo_white"></div>
			</div>
		);
	};

};


// ====================================================================================

let w = window.innerWidth;
let h = window.innerHeight;

let modules = TrinityEngine.createScene('#aaa', w, h);
let effects = TrinityEngine.createEffect('BOKEH', modules.renderer, modules.scene, modules.camera);

modules.renderer.domElement.className = 'ExhibitCanvas';
modules.renderer.toneMapping = THREE.ReinhardToneMapping;
modules.renderer.physicallyCorrectLights = true;
modules.renderer.shadowMap.enabled = true;

window.addEventListener('resize', function() {
	w = window.innerWidth;
	h = window.innerHeight;
	TrinityEngine.setupScene(w, h, modules.renderer, modules.camera, effects.composer);
});

// light
let light = TrinityEngine.createLight('DIRECT', '#fff', 2.5);
light.castShadow = true;
light.shadow.mapSize.width = 1024;
light.shadow.mapSize.height = 1024;
light.position.set(20, 20, 20);
light.shadow.camera.far = 1000;
modules.scene.add(light);

TrinityEngine.setupControls(
	'EXACT', modules.controls,
	{ x : 4.732049921108248, y : 3.9614543577672685, z : 17.075363821042263 },
	{ x : 4.769745339785276, y : 3.9711930844496743, z : 17.19859546598195 },
	//{ x : 0, y : 2, z : 0 },
	//{ x : 0, y : 95, z : 0 },
	{ enableZoom : false, rotateSpeed : -0.3 }
);

// ====================================================================================

let ground = TrinityEngine.createObject({
	Box : [40, 0.4, 100],
	MeshBasic : { color : '#06c', transparent : true, opacity : 0 }
	}, { position : { x : 0, y : 0, z : 0 } }
);

modules.scene.add(ground);

TrinityEngine.createEvent('click', ground, function(e) {
	if(ItemsLoader.focused) { return; }
	TrinityEngine.tweenControls('FIXED', modules.controls,
		{ x : e.point.x, y : 4, z : e.point.z },
		e.distance * 100, 'power1',
		function() {
			modules.controls.rotateSpeed = -0.3;
		}
	);
});

TrinityEngine.checkControls(modules.controls)

TrinityEngine.loadModel('models/space/scene.gltf', function(model) {
	model.scale.set(0.5, 0.5, 0.5);
	model.position.y = -0.01;
	var arr = TrinityEngine.findByType(model, 'Mesh', true);
	TrinityEngine.setupMaterial(arr, { castShadow : true, receiveShadow : true });
	arr.forEach(function(mesh) {
		if(mesh.name === 'Material2009') { mesh.material.metalness = 70 }
	});
	TrinityEngine.checkObjects(model);
	modules.scene.add(model)
});



let files = { space : 'models/space/scene.gltf' };


Object.keys(StallData).forEach(function(name) {
	let source = 'models/booths/'+ StallData[name].model_path +'/scene.gltf';
	files[name] = source;
});

function preLoad() {
	TrinityEngine.loadAll(files, function() {
		Object.keys(StallData).forEach(function(name) {
			var stall = files[name];
			TrinityEngine.setupObject(stall, StallData[name].transforms);
			modules.scene.add(stall);
		});
		
		setTimeout(function() {
			LoadingScreen.setValue(100);
			LoadingScreen.end(function() {
				ToastBox.append('Welcome to Arimac Virtual Exhibition!', 20000, 3000);
				ToastBox.append('Click on the ground to explore', 20000, 3800);
				ToastBox.append('Use search bar to find items faster', 20000, 3900);
			});
		}, 1000);

	}, function(e) {
		LoadingScreen.setValue(parseInt(e / 104 * 100));
	});

	ItemsLoader.makeCards(ItemsData, modules.controls);

	document.querySelector('.DetailBoxLink').addEventListener('click', function(e) {
		window.open(e.target.lang);
	});
};

window.c = modules.controls

function checkToLoad() {
	ItemsLoader.checkToLoad(
		modules.scene, ItemsData, modules.controls.target
	).forEach(function(item) {
		ToastBox.append('Loading Item... (' + item + ')');
	});
};

// ====================================================================================

// low end pcs
if(window.location.toString().indexOf('lowend=1') > -1) {
	effects.composer.passes[1].enabled = false;
}

function render() {
	requestAnimationFrame(render);
	effects.composer.render();
	//modules.renderer.render(modules.scene, modules.camera);
	ItemsLoader.displayCards(ItemsData, modules.camera, modules.renderer);
	checkToLoad();
}; render();













// ====================================================================================

export default App;