import { TrinityEngine } from "../engine/4.0";

let ItemsLoader = {};

let min_distance = 14; // load min distance
let max_distance = 20; // visible max distance

ItemsLoader.checkToLoad = function(scene, obj, pos) {
    var arr = [];
    Object.values(obj).forEach(function(item, i) {
        // get distance
        var a = item.transforms.position.x;
        var b = item.transforms.position.z;
        var x = pos.x; var y = pos.z;
        var d = Math.sqrt(Math.pow(a - x, 2) + Math.pow(b - y, 2));
        // load once if close
        if(d < min_distance && item.onLoad === undefined) {
            arr.push(Object.keys(obj)[i]);
            item.onLoad = true;
            let source = 'models/items/' + item.model_path;
            TrinityEngine.loadModel(source, function(model) {
                item.model = model;
                if(item.edit) { TrinityEngine.checkTransform(model); }
                TrinityEngine.setupObject(model, item.transforms);
                scene.add(model);
            });
        }
        // hide if far away
        if(d > max_distance && item.model) { item.model.visible = false; }
        // show if closer
        if(d < max_distance && item.model) { item.model.visible = true; }

    });
    return(arr);
};

ItemsLoader.makeCards = function(obj, ctrl) {
    Object.keys(obj).forEach(function(name, i) {
        var e = document.createElement('div');
        e.className = 'card';
        e.id = 'card_off';
        e.innerHTML = name;
        e.addEventListener('click', function() {
            ItemsLoader.focusToItem(name);
        });
        document.querySelector('#root').appendChild(e);
    });

    ItemsLoader.cards = document.querySelectorAll('.card');
    ItemsLoader.data  = obj;
    ItemsLoader.ctrl  = ctrl;

    document.querySelector('.DetailBoxBack').addEventListener('click', ItemsLoader.back);
};

ItemsLoader.displayCards = function(obj, camera, renderer) {
    if(ItemsLoader.cards.length === 0) { return; }
    Object.keys(obj).forEach(function(name, i) {
        let item = obj[name];
        let posi = obj[name].transforms.position;
        let dist = TrinityEngine.evalPoints('BETWEEN', posi, camera);
        var e = ItemsLoader.cards[i];
        if(dist < 10 && item.model && ItemsLoader.focused === false) {
            let pos = TrinityEngine.evalPoints('SCREEN', item.model, camera, renderer);
            if(
                pos.x > 0 && pos.y > 0
                &&
                pos.x < window.innerWidth - 120 && pos.y < window.innerHeight - 50
            ) {
                e.style.top = parseInt(pos.y) + 'px';
                e.style.left = parseInt(pos.x) + 'px';
                e.id = 'card_on';
            } else {
                e.id = 'card_off';
            }
        } else {
            e.id = 'card_off';
        }
    });
};

ItemsLoader.cards = [];
ItemsLoader.data  = {};
ItemsLoader.data  = null;
ItemsLoader.focused = false;

ItemsLoader.old = {};

ItemsLoader.focusToItem = function(name) {
    ItemsLoader.focused = true;
    document.querySelector('.ToastBox').style.display = 'none';
    document.querySelector('.SearchBox').style.display = 'none';
    document.querySelector('input').style.display = 'none';
    ItemsLoader.old.tar = ItemsLoader.ctrl.target.clone();
    ItemsLoader.old.obj = ItemsLoader.ctrl.object.position.clone();
    TrinityEngine.tweenControls(
        'EXACT', ItemsLoader.ctrl,
        ItemsLoader.data[name].transforms.position,
        ItemsLoader.data[name].visit_pose.camera,
        1000
    );
    ItemsLoader.ctrl.enableZoom = true;
    ItemsLoader.ctrl.rotateSpeed = 0.5;
    ItemsLoader.ctrl.maxPolarAngle = 1.567572092834932;
    ItemsLoader.ctrl.minPolarAngle = 0.4576121023704098;
    ItemsLoader.ctrl.maxDistance = 4;
    ItemsLoader.ctrl.minDistance = 2;
    document.querySelector('.DetailBox').style.display = 'block';
    document.querySelector('.DetailBoxHead').innerHTML = name;
    document.querySelector('.DetailBoxText').innerHTML = ItemsLoader.data[name].description;
    document.querySelector('.DetailBoxLink').lang = ItemsLoader.data[name].store_link;
};

ItemsLoader.back = function() {
    TrinityEngine.tweenControls(
        'EXACT', ItemsLoader.ctrl,
        ItemsLoader.old.tar,
        ItemsLoader.old.obj,
        1000
    );
    ItemsLoader.ctrl.enableZoom = false;
    ItemsLoader.ctrl.rotateSpeed = -0.3;
    ItemsLoader.ctrl.maxPolarAngle = 3.141592653589793;
    ItemsLoader.ctrl.minPolarAngle = 0;
    ItemsLoader.ctrl.minDistance = 0;
    ItemsLoader.ctrl.maxDistance = Infinity;
    document.querySelector('.DetailBox').style.display = 'none';
    ItemsLoader.focused = false;
    document.querySelector('.ToastBox').style.display = 'block';
    document.querySelector('.SearchBox').style.display = 'block';
    document.querySelector('input').style.display = 'block';
};











export default ItemsLoader;